const pluginCDN = require('./api.json').pluginCDN

/**
 * @module pluginStore InPageEdit
 */
var pluginStore = {
  /**
   * @module pluginStore.get 
   */
  get() {
    return $.ajax({
      url: pluginCDN + '/index.json',
      dataType: 'json',
      crossDomain: true,
      cache: false,
    })
  },
  saveCache(data) {
    var ipe = window.InPageEdit || {}
    ipe.cache = ipe.cache || {}
    ipe.cache.pluginList = data
    window.InPageEdit = ipe
  },
  loadCache() {
    var ipe = window.InPageEdit || {}
    ipe.cache = ipe.cache || {}
    return ipe.cache.pluginList
  },
  /**
   * @module pluginStore.load 
   * @param {String} name
   */
  load(name) {
    if (/^https?:\/\//.test(name)) {
      mw.loader.load(name)
      console.info('[InPageEdit] ', name)
    } else {
      const { loadScript } = require('../method/loadScript')
      loadScript(pluginCDN + '/plugins/' + name).then(
        () => console.info('[InPageEdit]  ' + name + ' '),
        err => console.warn('[InPageEdit]  ' + name + ' ', err)
      )
      console.info('[InPageEdit] ', name)
    }
  },
  /**
   * @module pluginStore.initUserPlugin 
   */
  initUserPlugin() {
    const { preference } = require('./preference')
    var userPlugins = preference.get('plugins')
    if (typeof userPlugins === 'object' && userPlugins.length > 0) {
      $.each(userPlugins, (key, val) => {
        pluginStore.load(val)
      })
    }
  },
}

module.exports = {
  pluginStore,
}
